* {
  box-sizing: border-box;
}

input,
select,
textarea,
button {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

body {
  font-family: $font-primary;
  font-size: rem(16px);
  color: $dark-alpha-80;
  background-color: $light-hex-100;
}

a {
  color: $brand-primary-200;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

b,
strong {
  font-weight: 700;
}

// Horizontal lines
//
// TODO-MDO: Remove `.rule` from everywhere and replace with `<hr>`s
hr,
.rule {
  height: 0;
  margin: 15px 0;
  overflow: hidden;
  background: transparent;
  border: 0;
  border-bottom: 1px solid $dark-alpha-10;
  @include clearfix();
}

//
// Remove most spacing between table cells.
//

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

button {
  cursor: pointer;
  // Remove border radius added by Chroma macOS
  border-radius: 0;
}

details {
  summary { cursor: pointer; }

  &:not([open]) {
    // Set details content hidden by default for browsers that don't do this
    > *:not(summary) { display: none !important; }
  }
}
